/* Inter font */
@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: local('Inter Thin'), local('Inter-Thin'),
       url('/fonts/Inter-Thin.woff2') format('woff2'),
       url('/fonts/Inter-Thin.woff') format('woff');
}

